.text-with-rule {
    display: flex;
    margin: auto;
    text-align: center;
    text-transform: uppercase;
    font-size: 0.87rem;
}

.text-with-rule::before, .text-with-rule::after {
    display: inline-block;
    flex: 1;
    margin: auto 0.6rem;
    border-top: 1px solid #adadad;
    content: '';
    vertical-align: middle;
}

.text-with-rule::before {
    margin-left: 0;
}

.text-with-rule::after {
    margin-right: 0;
}

a {
    position: relative;
    display: inline-block;
    color: #0033a0;
    text-decoration: none;
    transition: color 100ms linear;
}

.no-anchor-underline a, a.no-anchor-underline, a.anchor-minimal, a.text-with-icon {
    position: static;
    text-decoration: none;
}

.no-anchor-underline a::before, a.no-anchor-underline::before, a.anchor-minimal::before, a.text-with-icon::before {
    display: none;
    box-shadow: none;
}

.link-list a {
    position: static;
    text-decoration: none;
}

.link-list a::before {
    display: none;
    box-shadow: none;
}

a::before {
    position: absolute;
    bottom: 1px;
    left: 0;
    z-index: -1;
    width: 100%;
    height: 3px;
    box-shadow: 0 2px 0 currentColor;
    content: '';
    transition: transform 250ms cubic-bezier(0.39, 0.575, 0.565, 1), color 250ms linear;
    transition-delay: 100ms;
    transform: scaleX(0);
}

a:hover {
    color: #004ff8;
    transition: color 250ms linear;
}

a:hover::before {
    box-shadow: 0 2px 0 currentColor;
    transform: scaleX(1);
}

a:active {
    color: #0033a0;
    transition: color 100ms linear;
}

a:visited {
    color: #002b88;
    transition: color 100ms linear;
}

a.disabled, a[disabled] {
    opacity: 0.6;
    -webkit-filter: grayscale(0.6);
    filter: grayscale(0.6);
    pointer-events: none;
}

a.anchor-subtle {
    color: #474747;
}

a.anchor-subtle:hover {
    color: #757575;
}

a.anchor-button-subtle {
    text-transform: uppercase;
    letter-spacing: 0.6px;
}

.text-with-icon {
    position: relative;
    display: inline-flex;
    vertical-align: middle;
    align-self: center;
    align-items: center;
}

.text-with-icon .icon {
    position: relative;
    width: 1em;
    height: 1em;
}

.text-with-icon .icon.icon-left {
    margin-right: .3em;
    order: -1;
}

.text-with-icon .icon.icon-right {
    margin-left: .3em;
    order: 999;
}

.cta {
    text-decoration: none;
    text-transform: uppercase;
    letter-spacing: 2.4px;
    font-weight: 700;
    line-height: initial;
    cursor: pointer;
}

.cta_text {
    box-shadow: 0 4px 0 currentColor;
}

.cta .icon {
    width: 1em;
    height: 1em;
    font-size: 1em;
}

.cta .icon.icon-right {
    margin-left: 0.05em;
}

.link-list {
    margin: 0;
    padding: 0;
    list-style-type: none;
    display: inline-block;
    color: black;
    vertical-align: top;
    font-size: 0.87rem;
}

.link-list ul,
  .link-list ol {
    list-style-type: none;
}

.link-list:first-child:not(:last-child) {
    padding-right: 1em;
}

.link-list:not(:first-child):not(:last-child) {
    padding: 0 1em;
}

.link-list:last-child:not(:first-child) {
    padding-left: 1em;
}

.link-list li {
    display: flex;
    min-height: 1.75rem;
    text-transform: capitalize;
    align-items: center;
}

.link-list li a:not(.cta) {
    display: flex;
    flex: 0 1 auto;
    color: inherit;
    transition: color 100ms linear;
    align-items: center;
}

.link-list li a:not(.cta):hover {
    color: #474747;
}

.link-list li a:not(.cta):active {
    color: inherit;
}

.link-list .title {
    display: flex;
    text-transform: uppercase;
    font-weight: 700;
    align-items: stretch;
}

.link-list .title .cta {
    display: flex;
    flex: 0 1 auto;
    font-weight: normal;
    align-self: stretch;
}

.link-list .title .cta .icon {
    width: .8em;
    height: .8em;
}

.touchevents .link-list:first-child:not(:last-child), .touchevents .link-list:not(:first-child):not(:last-child), .touchevents .link-list:last-child:not(:first-child) {
    padding: 0;
}

.touchevents .link-list li {
    min-height: 3.125rem;
    align-items: stretch;
}

.touchevents .link-list li a:not(.cta) {
    display: flex;
    flex: 1;
    align-items: center;
    align-self: stretch;
}

.touchevents .link-list .title,
.touchevents .link-list li:first-child {
    display: flex;
    align-items: center;
}

.touchevents .link-list .title .cta {
    flex: 1;
}

.clampable_clamped-placeholder button {
    margin: 0;
    padding: 0;
    border: 0;
    background: transparent;
    color: #00205b;
